

function PortfolioList(props){

	console.log(props)
	console.log(props.propsValue);

	let{id, delay, path, alt, desc}=props.propsValue;
		// console.log(id)
		// console.log(desc)
		// console.log(desc.dt)
		// console.log(desc.dd)
	return (
		<li key={id} data-aos="fade-up" data-aos-delay={delay}>
			<a href="">
				<div className="photo"><img src={"./asset/images/"+path} alt={alt} /></div>
				<div className="desc">
					<dl>
						<dt>{desc.dt}</dt>
						<dd key={id}>{desc.dd}</dd>
					</dl>
				</div>
			</a>
		</li>
	);
}

export default PortfolioList;