

function KeyVisual(props){
	// console.log(props.propsValue)

	let data=props.propsValue;
	
	return (
		<div className="keyvisual">
			<ul>
				{/* <li>
					<img src="./asset/images/keyvisual.jpg" alt="메인1" />
				</li> */}
				{
					data.map((d, i) => <li key={i}> <img src={"./asset/images/"+d.path} alt={d.alt} /> </li> )
				}
				
			</ul>
		</div>
	);
}

export default KeyVisual;