import data from "./data";

function Title(props){

	console.log(props.propsValue)

	let {h2, p}=props.propsValue;
	let [p1, p2]=p.split('<br class="br">')

		// console.log(h2)
		console.log(p)


	return (
		<div className="title">
			<h2>{h2}</h2>
			<p>{p1}<br className="br" />{p2}</p>
		</div>
	);
}

export default Title;