import ServiceList from "./ServiceList";

function ServiceContent(props){
		console.log(props)
		console.log(props.propsValue)

		let data=props.propsValue;
	return (
		<div className="service_list">
			<ul>
				<ServiceList propsValue={data[0]}/>
				<ServiceList propsValue={data[1]}/>
				<ServiceList propsValue={data[2]}/>
				<ServiceList propsValue={data[3]}/>
			</ul>
		</div>
	);
}

export default ServiceContent;