

function HeaderText(props){
	// console.log(props.propsValue)

	let {main, sub, button}=props.propsValue
	return (
		<div className="text_zone">
			<p data-aos="fade-up" data-aos-delay="0">{main}</p>
			<h2 data-aos="fade-up" data-aos-delay="500">{sub.h2}<br /><span>{sub.span}</span></h2>
			<a href="" className="more" data-aos="fade-up" data-aos-delay="1000">{button}</a> 
		</div>
	);
}

export default HeaderText;