import data from "./data";

function BusinessList(props){
		// console.log(props.propsValue)
	let {id, delay, path, alt, desc}=props.propsValue;	



	return (
		<li key={id}data-aos="fade-up" data-aos-duration={delay}>
			<div className="icon"><a href=""><img src={"./asset/images/"+path} alt={alt} /></a></div>
			<div className="desc">
				<dl>
					{/* 
					<dt>Web Agency</dt>

					<dd>Desktop Web</dd>
					<dd>Mobile Web, App</dd>
					<dd>WordPress Development</dd>
					<dd>Shopping Mall</dd>
					 */}
					<dt>{desc.dt}</dt>
					{
						desc.dd.map((d, i) => <dd key={i}>{d}</dd>)
					}
				</dl>
			</div>
		</li>
	);
}

export default BusinessList;