import PortfolioList from "./PortfolioList";
import data from "./data";

function PortfolioContent(props){
	// console.log(props)
	// console.log(props.propsValue);

	let data=props.propsValue;

	
	return (
		<div className="content clearfix">
			<ul>
				<PortfolioList propsValue={data[0]} />
				<PortfolioList propsValue={data[1]} />
				<PortfolioList propsValue={data[2]} />
				<PortfolioList propsValue={data[3]} />
				<PortfolioList propsValue={data[4]} />
				<PortfolioList propsValue={data[5]} /> 
			</ul>
		</div>
	);
}

export default PortfolioContent;