import Navigation from "./Navigation";

function Menu(props){
	// console.log(props.propsValue)
	let data=props.propsValue;
	
	return (
		<div className="menu_zone_inner" data-aos="fade-up" data-aos-delay="1000">
			<h1 id="logo"><a href=""><img src="./asset/images/logo2.png" alt="logo" /></a></h1>
			<Navigation propsValue={data}/>
		</div>
	);
}

export default Menu;