

function ContactContent(){
	return (
		<div className="form clearfix">
			<form>
				<div className="input_wrap" data-aos="fade-up" data-aos-delay="0">
					<input type="text" id="name" placeholder="YOUR NAME*" />
					<input type="text" id="email" placeholder="YOUR E-MAIL*" />
					<input type="text" id="subject" placeholder="SUBJECT*" />
				</div>
				<div className="text_wrap" data-aos="fade-up" data-aos-delay="500">
					<textarea placeholder="YOUR MESSAGE*"></textarea>
				</div>
				<div className="submit" data-aos="fade-up" data-aos-delay="200">
					<input type="button" value="SEND MESSAGE" />
				</div>
			</form>
		</div>
	);
}

export default ContactContent;