

function ServiceList(props){

	let{id, delay, path, alt, desc}=props.propsValue;

	return (
		<li key={id} data-aos="fade-up" data-aos-delay={delay}>
			<a href="" className="photo"><img src={"./asset/images/"+path} alt={alt} /></a>
			<div className="desc">
				<dl>
					<dt>{desc.dt}</dt>
					<dd key={id}>{desc.dd}</dd>
				</dl>
			</div>
		</li>
	);
}

export default ServiceList;